import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import * as moment from 'moment';
import { Users } from '../models/users';
import { reject } from 'q';

moment.locale('en');

@Injectable({
  providedIn: 'root'
})

export class UsersService {

    constructor(private http: HttpClient) {
        
    }

    getCountUserApp() {
        return this.http.get<any>(`${environment.url_api}getCountUserApp`).pipe(map(result => {
          return result.Total;
        }));
    }
  
    getUsersApp(): Observable<Users[]> {
        return this.http.get<any>(`${environment.url_api}getUsuariosApp`).pipe(map(result => {
            return JSON.parse(JSON.stringify(result.UsuariosAPP)).map(item => {                
                return new Users(
                    item.id_usuario_app,
                    item.nombre,
                    item.usuario,
                    item.password,
                    item.isLogged,
                    item.image,
                    item.institucion,
                    item.cargo,
                    item.token,
                    item.status,
                    item.createAt
                );
            });
        }));
    }

    addUserApp(user: Users){
        let data = {
            nombre: user.nombre,
            usuario: user.usuario,
            password: user.password,
            image: user.image,
            institucion: user.institucion,
            cargo: user.cargo,
            createAt: user.createAt
        }
    
        return this.http.post<any>(`${environment.url_api}AddUsuarioApp`, data).pipe(
            map(result => {                           
                if(result.OK) {
                    return result.addUsuarioAPP;
                } else {
                    return reject;
                }
            })
        );  
    }

    updateUserApp(user: Users) {
        let data = {
            nombre: user.nombre,
            usuario: user.usuario,
            password: user.password,
            institucion: user.institucion,
            cargo: user.cargo,
        }

        console.log(data);
        
        return this.http.put<any>(`${environment.url_api}updateUsuarioApp/${user.id}`, data).pipe(map (result=> {                                              
            if(result.OK){
                return result;
            } else {
                return reject();
            }
        }));
    }


    deleteUserApp(id_usuario_app: string){
        return this.http.delete<any>(`${environment.url_api}deleteUsuarioApp/${id_usuario_app}`).pipe(
            map(result => {                
                return result;
            })
        );
    }

}